import { mapState, mapActions } from 'vuex';

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
export default {
	components: { uniSegmentedControl },
	data() {
		return {
			barHeight: 25,
			resList: [],
			resName: '',
			undoneWorkOrder: [],
			doneWorkOrder: [],
			undoneOrder: [],
			doneOrder: [],
			isRequest: true,
			items: ['待生产', '已完成'],
			current: 0,
			searchWord: '',
			resCount: false
		};
	},
	onLoad(option) {
		if (Object.keys(option).length != 0) {
			const resName = JSON.parse(option.resName);
			console.log(resName);
			console.log(this.resName);
			this.resName = resName;
		}
	},
	onShow() {
		console.log(this.resName);
		if (this.resName != '') {
			this.isRequest = true;
			this.GetUnstartList(this.resName["resourceName"]);
			this.isRequest = false;
			this.searchWord = '';
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'api', 'userInfo']),
	mounted() {
		this.getSystemStatusBarHeight();
		this.HasLogin();
		this.getResList();
	},
	methods: {
		resCountWrapClick() {
			this.resCount = false;
		},
		search() {
			if (this.current == 0) {
				this.undoneWorkOrder = [];
				this.undoneOrder.forEach(item => {
					if (item.workID.indexOf(this.searchWord) != -1) {
						this.undoneWorkOrder.push(item);
					}
				});
			} else {
				this.doneWorkOrder = [];
				this.doneOrder.forEach(item => {
					if (item.workID.indexOf(this.searchWord) != -1) {
						this.doneWorkOrder.push(item);
					}
				});
			}
		},
		onClickItem(e) {
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
			this.search();
		},
		getSystemStatusBarHeight() {
			var height = plus.navigator.getStatusbarHeight();
			this.barHeight = height;
		},
		showResCount() {
			this.resCount = this.resCount == true ? false : true;
		},
		selectRes() {
			uni.navigateTo({
				url: '../selectRes/resList?resList=' + JSON.stringify(this.resList)
			});
		},
		work(workItem) {
			uni.navigateTo({
				url: '../workDetail/work?workItem=' + JSON.stringify(workItem)
			});
		},
		HasLogin() {
			if (this.userInfo == null) {
				uni.redirectTo({
					url: '../login/login'
				});
			}
		},
		getResList() {
			console.log("获取列表");
			const _this = this;
			this.$HTTP({
				url: 'ResList',
				data: {
					usersysid: this.userInfo['userSysID']
				}
			}).then(resList => {
				_this.resList.push(...resList.data);
				if (_this.isRequest) {
					_this.resName = resList.data[0];
					_this.GetUnstartList(resList.data[0]['resourceName']);
				}
			});
		},
		GetUnstartList(resName) {
			if (this.isRequest) {
				const _this = this;
				this.doneWorkOrder = [];
				this.doneOrder = [];
				this.undoneOrder = [];
				this.undoneWorkOrder = [];
				const UserGuid = uni.getStorageSync('UserGuid');
				const UserEmpID = uni.getStorageSync('UserEmpID');
				this.$HTTP({
					url: 'GetUnstartList',
					data: {
						resName: resName
					}
				}).then(UnstartList => {
					console.log(UnstartList)
					if (UnstartList.data.length > 0) {
						UnstartList.data.forEach(item => {
							if (item.taskFinishState == 4 || item.taskFinishState == 5) {
								_this.doneWorkOrder.push(item);
								_this.doneOrder.push(item);
							} else {
								_this.undoneWorkOrder.push(item);
								_this.undoneOrder.push(item);
							}
						});
					}
				});
			}
		},
		downLoadWork() {
			console.log('选取要拉取的工单');
			console.log(this.resName);
			uni.navigateTo({
				url: '../downLoadWork/downLoadWork?resName=' + this.resName + '&dayshift=' + this.resName["dayshift"]
			});
		}
	}
};
